import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import Tables from "./Tables";
import PlayBingo from "./PlayBingo";
import { useState } from "react";
import { useEffect } from "react";
import { useLayoutEffect } from "react";
import axios from "axios";
import { css, cx } from "@emotion/css";

const RefactorBoardOfNumbers = ({
  valueOfNumbers,
  numberOfSeconds,
  stopStart,
  selectedLanguage,
  handleEndGame,
  count,
  setCount,
}) => {
  //const [count, setCount] = useState(0);
  //const [countSaved, setCountSaved] = useState(0);
  const [intervalId, setIntervalId] = useState(null);
  const [currentNumbers, setCurrentNumbers] = useState([]);
  //const [previousStopStart, setPreviousStopStart] = useState("start")
  //console.log(valueOfNumbers);

  const API_KEY = "AIzaSyAYCgJT_eF2u7qBuDFyBCBTGDLHahHbOeg";

  const speakText = async (text, languageCode) => {
    try {
      const response = await axios.post(
        `https://texttospeech.googleapis.com/v1/text:synthesize?key=${API_KEY}`,
        {
          input: { text },
          voice: { languageCode, ssmlGender: "MALE" },
          audioConfig: { audioEncoding: "MP3" },
        }
      );
      console.log("Response:", response);

      if (response.status !== 200) {
        throw new Error(`Request failed with status code ${response.status}`);
      }

      const audioContent = response.data.audioContent;
      const audio = new Audio(`data:audio/mp3;base64,${audioContent}`);
      audio.play();
    } catch (error) {
      console.error(
        "Error during text-to-speech request:",
        error.message || error
      );
    }
  };

  //console.log("valueofNumbers", valueOfNumbers);

  useLayoutEffect(() => {
    let interval = null;

    if (stopStart === "start" || stopStart === "pokracuj") {
      if (intervalId) {
        clearInterval(intervalId);
      }

      if (stopStart === "start") {
        //setCount((prevCount) => 0);
        //setCountSaved(0);
        //console.log("start")
      }
      interval = setInterval(() => {
        setCount((prevCount) => {
          if (prevCount < valueOfNumbers.length - 1) {
            return prevCount + 1;
          } else {
            clearInterval(interval);

            handleEndGame();
            return prevCount;
          }
        });
        /*
            setCountSaved(prev=> {
            if (prev < valueOfNumbers.length - 1 )  
              {return prev+1}
            else {
              return prev
            }}
            
             ); */
      }, numberOfSeconds * 1000);
      setIntervalId(interval);
    }

    if (stopStart === "reset") {
      if (intervalId) clearInterval(intervalId);
      setIntervalId(null);
      //setCount(0);
      // setCountSaved(0);
    }

    if (stopStart === "koniecHry") {
      if (intervalId) {
        clearInterval(intervalId);
        setIntervalId(null);
        //setCount(0);
      }
    }

    if (stopStart === "pauza") {
      if (intervalId) clearInterval(intervalId);
    }

    return () => {
      clearInterval(interval);
    };
  }, [stopStart, numberOfSeconds]);

  const convertNumberToSlovakWords = (number) => {
    const numberToWordsMapSK = {
      1: "jeden",
      2: "dva",
      3: "tri",
      4: "tyri",
      5: "p",
      6: "es",
      7: "sedem",
      8: "osem",
      9: "dev",
      10: "desa",
      11: "jedens",
      12: "dvans",
      13: "trins",
      14: "trns",
      15: "ptns",
      16: "estns",
      17: "sedemns",
      18: "osemns",
      19: "devtns",
      20: "dvadsa",
      21: "dvadsa jeden",
      22: "dvadsa dva",
      23: "dvadsa tri",
      24: "dvadsa tyri",
      25: "dvadsa p",
      26: "dvadsa es",
      27: "dvadsa sedem",
      28: "dvadsa osem",
      29: "dvadsa dev",
      30: "tridsa",
      31: "tridsa jeden",
      32: "tridsa dva",
      33: "tridsa tri",
      34: "tridsa tyri",
      35: "tridsa p",
      36: "tridsa es",
      37: "tridsa sedem",
      38: "tridsa osem",
      39: "tridsa dev",
      40: "tyridsa",
      41: "tyridsa jeden",
      42: "tyridsa dva",
      43: "tyridsa tri",
      44: "tyridsa tyri",
      45: "tyridsa p",
      46: "tyridsa es",
      47: "tyridsa sedem",
      48: "tyridsa osem",
      49: "tyridsa dev",
      50: "pdesiat",
      51: "pdesiat jeden",
      52: "pdesiat dva",
      53: "pdesiat tri",
      54: "pdesiat tyri",
      55: "pdesiat p",
      56: "pdesiat es",
      57: "pdesiat sedem",
      58: "pdesiat osem",
      59: "pdesiat dev",
      60: "esdesiat",
      61: "esdesiat jeden",
      62: "esdesiat dva",
      63: "esdesiat tri",
      64: "esdesiat tyri",
      65: "esdesiat p",
      66: "esdesiat es",
      67: "esdesiat sedem",
      68: "esdesiat osem",
      69: "esdesiat dev",
      70: "sedemdesiat",
      71: "sedemdesiat jeden",
      72: "sedemdesiat dva",
      73: "sedemdesiat tri",
      74: "sedemdesiat tyri",
      75: "sedemdesiat p",
    };
    return numberToWordsMapSK[number] || number.toString();
  };

  const formatTextForSlovak = (text) => {
    const [letter, number] = text.split("-");
    const numberInWords = convertNumberToSlovakWords(parseInt(number, 10));

    if (text.includes("Koniec hry")) {
      return `${letter} ${numberInWords}. Koniec hry`;
    }
    return `${letter} ${numberInWords}`;
  };

  const convertNumberToEnglishWords = (number) => {
    const numberToWordsMapEN = {
      1: "one",
      2: "two",
      3: "three",
      4: "four",
      5: "five",
      6: "six",
      7: "seven",
      8: "eight",
      9: "nine",
      10: "ten",
      11: "eleven",
      12: "twelve",
      13: "thirteen",
      14: "fourteen",
      15: "fifteen",
      16: "sixteen",
      17: "seventeen",
      18: "eighteen",
      19: "nineteen",
      20: "twenty",
      21: "twenty-one",
      22: "twenty-two",
      23: "twenty-three",
      24: "twenty-four",
      25: "twenty-five",
      26: "twenty-six",
      27: "twenty-seven",
      28: "twenty-eight",
      29: "twenty-nine",
      30: "thirty",
      31: "thirty-one",
      32: "thirty-two",
      33: "thirty-three",
      34: "thirty-four",
      35: "thirty-five",
      36: "thirty-six",
      37: "thirty-seven",
      38: "thirty-eight",
      39: "thirty-nine",
      40: "forty",
      41: "forty-one",
      42: "forty-two",
      43: "forty-three",
      44: "forty-four",
      45: "forty-five",
      46: "forty-six",
      47: "forty-seven",
      48: "forty-eight",
      49: "forty-nine",
      50: "fifty",
      51: "fifty-one",
      52: "fifty-two",
      53: "fifty-three",
      54: "fifty-four",
      55: "fifty-five",
      56: "fifty-six",
      57: "fifty-seven",
      58: "fifty-eight",
      59: "fifty-nine",
      60: "sixty",
      61: "sixty-one",
      62: "sixty-two",
      63: "sixty-three",
      64: "sixty-four",
      65: "sixty-five",
      66: "sixty-six",
      67: "sixty-seven",
      68: "sixty-eight",
      69: "sixty-nine",
      70: "seventy",
      71: "seventy-one",
      72: "seventy-two",
      73: "seventy-three",
      74: "seventy-four",
      75: "seventy-five",
    };
    return numberToWordsMapEN[number] || number.toString();
  };

  const formatTextForEnglish = (text) => {
    const [letter, number] = text.split("-");
    const numberInWords = convertNumberToEnglishWords(parseInt(number, 10));

    if (text.includes("End of game")) {
      return `${letter} ${numberInWords}. End of game`;
    }
    return `${letter} ${numberInWords}`;
  };

  useEffect(() => {
    //console.log("speechcount", count);
    if (
      (stopStart === "start" || stopStart === "pokracuj") &&
      valueOfNumbers[count]
    ) {
      const numberValue = valueOfNumbers[count];

      const utteranceText =
        selectedLanguage === "sk-SK"
          ? formatTextForSlovak(numberValue)
          : formatTextForEnglish(numberValue);

      //console.log("speech");
      //console.log("count", count);

      speakText(utteranceText, selectedLanguage);
    }
  }, [count, stopStart, selectedLanguage]);

  useLayoutEffect(() => {
    const soFarNumbersArray = valueOfNumbers.slice(0, count);
    setCurrentNumbers(soFarNumbersArray);
  }, [count]);

  /*
      useEffect(() => {
        if (stopStart === "stop") {
          if (intervalId) clearInterval(intervalId); 
          setCount(0); 
        }
      }, [stopStart]);
*/

  //const renderText = stopStart === "stop" ? <div></div> : <div>{valueOfNumbers[count]}</div>

  const styleKolo = css`
    font-size: 3rem;
    font-weight: bold;
    color: var(--farba-text);
    margin: 1rem auto;
  `;

  const styleAktualneCislo = css`
    font-size: 4rem;
    margin: 1.5rem auto;
    background-color: var(--farba-volane-cislo);
    color: white;
    padding: 1.5rem;
    border-radius: 10%;
    display: flex;
    justify-content: center;
    width: 9rem;
  `;

  const styleRenderText = css`
    margin: auto;

    display: flex;
    flex-direction: column;
  `;

  const styleHistory = css`
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    justify-content: center;
    gap: 2rem;
    margin: 2.5rem auto;
    max-width: 70%;

    div {
      font-size: 3rem;
      background-color: var(--farba-historia-cislo-pozadie);
      border-radius: 10%;
      padding: 1.5rem;
      color: var(--farba-historia-cislo);
      border: 2px solid var(--farba-historia-cislo-border);
      display: flex;
      justify-content: center;
      width: 7rem;
    }
  `;

  const styleAktualneCisloPosledne = css`
    width: max-content;
  `;

  const renderText =
    stopStart === "reset" ? (
      <div></div>
    ) : (
      <>
        <div className={styleKolo}>{`Kolo: ${count + 1}`}</div>
        {count !== 74 ? (
          <div className={styleAktualneCislo}>
            <span>{`${valueOfNumbers[count]}`}</span>
          </div>
        ) : (
          <div className={cx(styleAktualneCislo, styleAktualneCisloPosledne)}>
            <span>{`${valueOfNumbers[count]}`}</span>
          </div>
        )}
      </>
    );

  const styledLargeDiv = css``;

  return (
    <>
      <div>
        <div className={styleRenderText}>
          {/* <div>{console.log("countSaved", count)}</div> */}
          {renderText}
        </div>
        <div className={styleHistory}>
          {Array.isArray(currentNumbers) &&
            currentNumbers
              .slice()
              .reverse()
              .map((element, index) => (
                <div>
                  <span>
                    {index !== currentNumbers.length - 1
                      ? `${element}`
                      : element}
                  </span>
                </div>
              ))}
        </div>
      </div>
    </>
  );
};

export default RefactorBoardOfNumbers;
